.aside {
  display: flex;
  width: 240px;
  flex: 0 0 auto;
  margin-right: calc(var(--gap) * 3);

  @include ipad() {
    display: none;
  }

  &__wrapper {
    position: sticky;
    width: 100%;
    top: 82px;
    display: flex;
    flex-direction: column;
    max-height: calc(100vh - 110px);
  }

  &__content {
    flex: 1 1 auto;
    padding: 0 var(--gap);
  }

  &__footer {
    flex: 0 0 auto;
    padding: 0 var(--gap) 0 calc(var(--gap) * 3);
  }
}
