.main {
  display: flex;
  flex: 1 1 auto;
  width: calc(100% - 264px);

  &__content {
    flex: 1 1 auto;
    width: calc(100% - 300px);
    padding-right: calc(var(--gap) * 3);

    @include ipad() {
      padding-right: 0;
    }
  }

  &__aside {
    flex: 0 0 auto;
    width: 300px;

    @include ipad() {
      position: fixed;
      top: 86px;
      transform: translateX(100%);
      transition: transform 0.6s, opacity 0.6s;
      height: calc(100vh - 86px);
      width: 100vw;
      right: 0;
      overflow-y: auto;
      background-color: var(--bg-color);
      padding: var(--gap) calc(var(--gap) * 0.5);
      opacity: 0;
      padding-bottom: 64px;
      z-index: 10;
    }
  }
}

.open-aside-menu {
  .main {
    &__aside {
      @include ipad() {
        opacity: 1;
        transform: translateX(0%);
      }
    }
  }
}
