.header {
  position: fixed;
  width: 100%;
  background-color: var(--bg-black-color);
  height: 56px;
  padding: calc(var(--gap) * 0.5) 0;
  z-index: 100;

  @include ipad() {
    height: 86px;
    padding: calc(var(--gap) * 1.5) 0;
  }

  &__container {
    padding-left: calc(var(--gap) * 4);
    height: 100%;
    display: flex;
    align-items: center;

    @include ipad() {
      align-items: flex-end;
      padding-left: calc(var(--gap) * 2);
    }
  }

  &__logo {
    width: 146px;
    height: 25px;
    margin-right: 82px;
    img {
      width: 100%;
      height: 100%;
    }

    @include ipad() {
      margin-bottom: calc(var(--gap) * 0.5);
    }
  }

  &__nav {
    display: flex;

    @include ipad() {
      display: none;
    }
  }

  &__nav-link {
    display: block;
    padding: var(--gap) calc(var(--gap) * 2);
    color: var(--white-color);
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
  }

  &__aside {
    margin-left: auto;
    min-width: 40px;
    position: relative;
  }

  &__user {
    display: flex;
    align-items: center;
  }

  &__close-button {
    display: none;
    position: absolute;
    width: 32px;
    height: 32px;
    z-index: 0;

    @include ipad() {
      display: block;
    }

    &:after,
    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 0px;
      height: 3px;
      border-radius: 3px;
      background-color: var(--white-color);
      transition: width 0.3s;
    }

    &:after {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &:before {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }

  &__user-photo {
    position: relative;
    width: 32px;
    height: 32px;
    border-radius: var(--border-radius-small);
    overflow: hidden;
    flex: 0 0 auto;
    margin-right: var(--gap);
    transition: opacity 0.6s;

    img {
      object-fit: cover;
      object-position: center;
      width: 100%;
      height: 100%;
    }
  }

  &__user-info {
    @include ipad() {
      display: none;
    }
  }

  &__user-name {
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: var(--white-color);
  }

  &__user-position {
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    color: var(--text-light-white-color);
  }
}

.open-aside-menu {
  .header {
    &__user-photo {
      @include ipad() {
        opacity: 0;
      }
    }

    &__close-button {
      z-index: 5;
      @include ipad() {
        &:after,
        &:before {
          width: 40px;
        }
      }
    }
  }
}
