.button {
  display: block;
  padding: var(--gap) calc(var(--gap) * 3);
  white-space: nowrap;
  height: 36px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.2px;
  color: var(--white-color);
  text-align: center;
  background-color: var(--main-color);
  border-radius: var(--border-radius-small);

  &--wide {
    width: 100%;
  }

  &--secondary {
    color: var(--text-color);
    background-color: var(--bg-color);
  }
}
