.social {
  display: flex;
  padding-bottom: calc(var(--gap) * 2);
  border-bottom: 1px solid var(--light-gray-color);
  &__list {
    display: flex;
  }

  &__item {
    margin-right: calc(var(--gap) * 1.5);

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: flex;
    width: 24px;
    height: 24px;
    fill: var(--text-color);
  }

  &__aside {
    margin-left: auto;
  }
}
