.category {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__icon {
    width: 24px;
    height: 24px;
    margin-right: var(--gap);
    font-weight: 500;
    font-size: 15px;
    line-height: 24px;
    color: var(--text-color);
  }

  &__title {
    display: flex;
    align-items: center;
  }

  &__date {
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--text-light-color);
  }
}
