:root {
  --main-color: #92bf20;
  --text-color: rgba(17, 32, 46, 0.8);
  --text-light-color: rgba(17, 32, 46, 0.6);
  --text-light-white-color: rgba(255, 255, 255, 0.6);
  --white-color: #ffffff;
  --black-color: #212121;
  --bg-color: #f4f6fa;
  --bg-black-color: #212121;
  --light-gray-color: #e0e0e0;
  --border-color: #eff0f6;

  --box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.04);

  --font-inter: "inter", Arial, sans-serif;
  --font-raleway: "raleway", Arial, sans-serif;

  --gap: 8px;

  --border-radius: 16px;
  --border-radius-small: 8px;
}
