* {
  box-sizing: border-box;
}

html {
  font-size: 18px;
}
html,
body {
  height: 100%;
}
body {
  font-family: var(--font-inter);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: var(--bg-color);
  color: var(--black-color);
}
ul {
  margin: 0;
  padding: 0;
}
ul li {
  list-style: none;
}
button {
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
svg {
  width: 100%;
  height: 100%;
}
