.aside-menu-button {
  position: fixed;
  bottom: 200px;
  right: 0;
  transform: translate(100%, 50%);
  transition: transform 0.5s;
  background-color: var(--main-color);
  width: 48px;
  height: 48px;
  border-radius: var(--border-radius-small) 0 0 var(--border-radius-small);
  padding: var(--gap);
  fill: var(--white-color);

  @include ipad() {
    transform: translate(0%, -50%);
  }
}

.open-aside-menu {
  .aside-menu-button {
    @include ipad() {
      transform: translate(100%, -50%);
    }
  }
}
