.main-slider {
  position: relative;
  width: 100%;
  background-color: var(--white-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: calc(var(--gap) * 4);
  height: 285px;
  overflow: hidden;

  @include desctopBig() {
    height: 188px;
  }

  @include ipad() {
    height: 141px;
  }

  &__list {
    padding: 6px 0;
    height: 100%;

    @include ipad() {
      padding: 0;
    }
  }

  &__item {
    position: relative;
    display: flex !important;
    overflow: hidden;
    align-items: center;
    height: 100%;
  }

  &__img {
    width: 357px;
    height: 274px;
    flex: 0 0 auto;
    margin-right: calc(var(--gap) * 4);

    @include desctopBig() {
      width: 235px;
      height: 180px;
      margin-right: calc(var(--gap) * 3);
    }

    @include ipad() {
      position: absolute;
      width: 208px;
      height: 160px;
      top: -10px;
      right: -30px;
      margin-right: 0;
      opacity: 0.2;
    }

    img {
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: calc(var(--gap) * 4.5);
    flex: 1 1 auto;
    height: 100%;

    @include desctopBig() {
      padding: calc(var(--gap) * 3);
      padding-right: calc(var(--gap) * 12);
    }
  }

  &__title {
    font-family: var(--font-raleway);
    font-weight: 800;
    font-size: 40px;
    line-height: 47px;
    margin-bottom: calc(var(--gap) * 1.5);

    @include desctopBig() {
      font-size: 32px;
      line-height: 38px;
    }

    @include ipad() {
      font-size: 24px;
      line-height: 24px;
    }

    span {
      color: var(--main-color);
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;

    @include desctopBig() {
      font-size: 16px;
      line-height: 20px;
    }

    @include ipad() {
      font-size: 13px;
      line-height: 16px;
    }
  }

  &__navigation {
    display: flex;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: 12px;

    @include ipad() {
      bottom: calc(var(--gap) * 0.5);
    }
  }

  &__arrow {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 auto;

    @include desctopBig() {
      width: 16px;
      height: 16px;
    }

    @include ipad() {
      display: none;
    }
  }

  .slick-list,
  .slick-track,
  .slick-slide > div {
    height: 100%;
  }

  .slick-dots {
    display: flex;

    li {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      @include desctopBig() {
        width: 16px;
        height: 16px;
      }

      &.slick-active {
        button {
          background-color: var(--main-color);
        }
      }

      button {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: var(--light-gray-color);
        color: transparent;
        transition: background-color 0.3s;

        @include desctopBig() {
          width: 8px;
          height: 8px;
        }
      }
    }
  }
}
