.nav-mobile {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 64px;
  display: none;
  background-color: var(--bg-color);
  box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.05);

  @include ipad() {
    display: flex;
  }

  &__item {
    display: flex;
    flex: 1 1 auto;
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1 1 auto;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: var(--text-color);
  }

  &__icon {
    flex: 0 0 auto;
    width: 24px;
    height: 24px;
  }

  &__item--active &__link {
    background-color: var(--white-color);
    color: var(--main-color);
  }
}
