.container {
  position: relative;
  margin: 0 auto;
  width: 100%;
  padding: 0 calc(var(--gap) * 1);
  max-width: 1608px;

  &--main {
    padding: 80px calc(var(--gap) * 1) calc(var(--gap) * 3);
    display: flex;
    flex: 1 1 auto;

    @include ipad() {
      padding: 94px calc(var(--gap) * 0.5) calc(var(--gap) * 9);
    }
  }

  @include desctopBig() {
    max-width: 1264px;
  }
}
