.subtitle-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 36px;

  &__title {
    font-family: var(--font-raleway);
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: var(--text-color);
  }

  &__button {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: var(--text-color);
    padding: var(--gap) 0;

    @include phone() {
      display: none;
    }
  }
}
