.card {
  display: flex;
  flex-direction: column;
  background-color: var(--white-color);
  padding: calc(var(--gap) * 3) calc(var(--gap) * 2);
  box-shadow: var(--box-shadow);
  border-radius: var(--border-radius);
  border: 1px solid var(--border-color);

  &__footer {
    margin-top: auto;
  }

  &__img {
    width: 100%;
    height: 160px;
    border-radius: calc(var(--border-radius-small) * 1.5);
    margin-bottom: calc(var(--gap) * 1.5);
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    font-family: var(--font-raleway);
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    margin-bottom: var(--gap);
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: var(--text-color);
    margin-bottom: calc(var(--gap) * 2);
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__category {
    margin-bottom: calc(var(--gap) * 1.5);
  }
}
