.employee {
  display: flex;

  &__photo {
    flex: 0 0 auto;
    width: 36px;
    height: 36px;
    border-radius: var(--border-radius-small);
    margin-right: calc(var(--gap) * 1.5);

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    flex: 1 1 auto;
  }

  &__name {
    position: relative;
    font-weight: 600;
    font-size: 15px;
    line-height: 16px;
    height: 19px;

    span {
      position: absolute;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &__position {
    position: relative;
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    height: 16px;
    color: var(--text-light-color);

    span {
      position: absolute;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
