.doocument-link {
  display: flex;
  align-items: center;
  padding: var(--gap);
  border-radius: var(--border-radius-small);
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  transition: background-color 0.3s;

  &:hover,
  &--active {
    background-color: var(--bg-color);
  }

  &__icon {
    width: 24px;
    height: 24px;
    fill: var(--text-color);
    margin-right: calc(var(--gap) * 0.5);
  }
}
