.cards {
  display: flex;
  flex-wrap: wrap;
  padding-top: calc(var(--gap) * 1.5);
  margin: 0 -12px;

  @include ipad() {
    justify-content: center;
    margin: 0 -4px;
  }

  &__item {
    width: 318px;
    margin: 0 calc(var(--gap) * 1.5) calc(var(--gap) * 3);

    @include ipad() {
      margin: 0 calc(var(--gap) * 0.5) calc(var(--gap) * 1.5);
    }

    @include phone() {
      width: 100%;
    }
  }
}
