:root {
  --main-color: #92bf20;
  --text-color: rgba(17, 32, 46, 0.8);
  --text-light-color: rgba(17, 32, 46, 0.6);
  --text-light-white-color: rgba(255, 255, 255, 0.6);
  --white-color: #ffffff;
  --black-color: #212121;
  --bg-color: #f4f6fa;
  --bg-black-color: #212121;
  --light-gray-color: #e0e0e0;
  --border-color: #eff0f6;
  --box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.04);
  --font-inter: "inter", Arial, sans-serif;
  --font-raleway: "raleway", Arial, sans-serif;
  --gap: 8px;
  --border-radius: 16px;
  --border-radius-small: 8px;
}

@font-face {
  font-family: "inter";
  src: url("../fonts/inter.woff") format("woff2"), url("../fonts/inter.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "inter";
  src: url("../fonts/Inter-Black.woff2") format("woff2"), url("../fonts/Inter-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "inter";
  src: url("../fonts/Inter-ExtraBold.woff2") format("woff2"), url("../fonts/Inter-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "raleway";
  src: url("../fonts/Raleway-Bold.woff2") format("woff2"), url("../fonts/Raleway-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "raleway";
  src: url("../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../fonts/Raleway-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 18px;
}

html,
body {
  height: 100%;
}

body {
  font-family: var(--font-inter);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: var(--bg-color);
  color: var(--black-color);
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

svg {
  width: 100%;
  height: 100%;
}

.aside-menu-button {
  position: fixed;
  bottom: 200px;
  right: 0;
  transform: translate(100%, 50%);
  transition: transform 0.5s;
  background-color: var(--main-color);
  width: 48px;
  height: 48px;
  border-radius: var(--border-radius-small) 0 0 var(--border-radius-small);
  padding: var(--gap);
  fill: var(--white-color);
}

.aside {
  display: flex;
  width: 240px;
  flex: 0 0 auto;
  margin-right: calc(var(--gap) * 3);
}

.aside__wrapper {
  position: sticky;
  width: 100%;
  top: 82px;
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 110px);
}

.aside__content {
  flex: 1 1 auto;
  padding: 0 var(--gap);
}

.aside__footer {
  flex: 0 0 auto;
  padding: 0 var(--gap) 0 calc(var(--gap) * 3);
}

.button {
  display: block;
  padding: var(--gap) calc(var(--gap) * 3);
  white-space: nowrap;
  height: 36px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.2px;
  color: var(--white-color);
  text-align: center;
  background-color: var(--main-color);
  border-radius: var(--border-radius-small);
}

.button--wide {
  width: 100%;
}

.button--secondary {
  color: var(--text-color);
  background-color: var(--bg-color);
}

.card-aside {
  background-color: var(--white-color);
  padding: calc(var(--gap) * 3) var(--gap);
  border: 1px solid var(--border-color);
  box-shadow: var(--box-shadow);
  border-radius: var(--border-radius);
  margin-bottom: calc(var(--gap) * 2);
}

.card-aside__title {
  font-family: var(--font-raleway);
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  margin-bottom: calc(var(--gap) * 2.5);
  text-transform: uppercase;
  color: var(--text-color);
  padding: 0 var(--gap);
}

.card-aside__content {
  padding: 0 var(--gap);
}

.card-aside__content--padding-0 {
  padding: 0;
}

.card-aside__footer {
  margin-top: calc(var(--gap) * 2.5);
  padding: 0 var(--gap);
}

.card-aside__main-photo {
  position: relative;
  width: 100%;
  max-width: 400px;
  overflow: hidden;
  border-radius: var(--border-radius-small);
}

.card-aside__main-photo:after {
  content: "";
  display: block;
  padding-top: 100%;
}

.card-aside__main-photo img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card {
  display: flex;
  flex-direction: column;
  background-color: var(--white-color);
  padding: calc(var(--gap) * 3) calc(var(--gap) * 2);
  box-shadow: var(--box-shadow);
  border-radius: var(--border-radius);
  border: 1px solid var(--border-color);
}

.card__footer {
  margin-top: auto;
}

.card__img {
  width: 100%;
  height: 160px;
  border-radius: calc(var(--border-radius-small) * 1.5);
  margin-bottom: calc(var(--gap) * 1.5);
  overflow: hidden;
}

.card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__title {
  font-family: var(--font-raleway);
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  margin-bottom: var(--gap);
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__text {
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: var(--text-color);
  margin-bottom: calc(var(--gap) * 2);
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__category {
  margin-bottom: calc(var(--gap) * 1.5);
}

.cards {
  display: flex;
  flex-wrap: wrap;
  padding-top: calc(var(--gap) * 1.5);
  margin: 0 -12px;
}

.cards__item {
  width: 318px;
  margin: 0 calc(var(--gap) * 1.5) calc(var(--gap) * 3);
}

.category {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.category__icon {
  width: 24px;
  height: 24px;
  margin-right: var(--gap);
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: var(--text-color);
}

.category__title {
  display: flex;
  align-items: center;
}

.category__date {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: var(--text-light-color);
}

.container {
  position: relative;
  margin: 0 auto;
  width: 100%;
  padding: 0 calc(var(--gap) * 1);
  max-width: 1608px;
}

.container--main {
  padding: 80px calc(var(--gap) * 1) calc(var(--gap) * 3);
  display: flex;
  flex: 1 1 auto;
}

.copyright {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: var(--text-light-color);
  padding-top: calc(var(--gap) * 2);
}

.doocument-link {
  display: flex;
  align-items: center;
  padding: var(--gap);
  border-radius: var(--border-radius-small);
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  transition: background-color 0.3s;
}

.doocument-link:hover,
.doocument-link--active {
  background-color: var(--bg-color);
}

.doocument-link__icon {
  width: 24px;
  height: 24px;
  fill: var(--text-color);
  margin-right: calc(var(--gap) * 0.5);
}

.employee-list__item {
  margin-bottom: calc(var(--gap) * 2);
}

.employee-list__item:last-child {
  margin-bottom: 0;
}

.employee {
  display: flex;
}

.employee__photo {
  flex: 0 0 auto;
  width: 36px;
  height: 36px;
  border-radius: var(--border-radius-small);
  margin-right: calc(var(--gap) * 1.5);
}

.employee__photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.employee__content {
  flex: 1 1 auto;
}

.employee__name {
  position: relative;
  font-weight: 600;
  font-size: 15px;
  line-height: 16px;
  height: 19px;
}

.employee__name span {
  position: absolute;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.employee__position {
  position: relative;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  height: 16px;
  color: var(--text-light-color);
}

.employee__position span {
  position: absolute;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header {
  position: fixed;
  width: 100%;
  background-color: var(--bg-black-color);
  height: 56px;
  padding: calc(var(--gap) * 0.5) 0;
  z-index: 100;
}

.header__container {
  padding-left: calc(var(--gap) * 4);
  height: 100%;
  display: flex;
  align-items: center;
}

.header__logo {
  width: 146px;
  height: 25px;
  margin-right: 82px;
}

.header__logo img {
  width: 100%;
  height: 100%;
}

.header__nav {
  display: flex;
}

.header__nav-link {
  display: block;
  padding: var(--gap) calc(var(--gap) * 2);
  color: var(--white-color);
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
}

.header__aside {
  margin-left: auto;
  min-width: 40px;
  position: relative;
}

.header__user {
  display: flex;
  align-items: center;
}

.header__close-button {
  display: none;
  position: absolute;
  width: 32px;
  height: 32px;
  z-index: 0;
}

.header__close-button:after,
.header__close-button:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 0px;
  height: 3px;
  border-radius: 3px;
  background-color: var(--white-color);
  transition: width 0.3s;
}

.header__close-button:after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.header__close-button:before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.header__user-photo {
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: var(--border-radius-small);
  overflow: hidden;
  flex: 0 0 auto;
  margin-right: var(--gap);
  transition: opacity 0.6s;
}

.header__user-photo img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

.header__user-name {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: var(--white-color);
}

.header__user-position {
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  color: var(--text-light-white-color);
}

.open-aside-menu .header__close-button {
  z-index: 5;
}

.main-slider {
  position: relative;
  width: 100%;
  background-color: var(--white-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: calc(var(--gap) * 4);
  height: 285px;
  overflow: hidden;
}

.main-slider__list {
  padding: 6px 0;
  height: 100%;
}

.main-slider__item {
  position: relative;
  display: flex !important;
  overflow: hidden;
  align-items: center;
  height: 100%;
}

.main-slider__img {
  width: 357px;
  height: 274px;
  flex: 0 0 auto;
  margin-right: calc(var(--gap) * 4);
}

.main-slider__img img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.main-slider__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: calc(var(--gap) * 4.5);
  flex: 1 1 auto;
  height: 100%;
}

.main-slider__title {
  font-family: var(--font-raleway);
  font-weight: 800;
  font-size: 40px;
  line-height: 47px;
  margin-bottom: calc(var(--gap) * 1.5);
}

.main-slider__title span {
  color: var(--main-color);
}

.main-slider__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
}

.main-slider__navigation {
  display: flex;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 12px;
}

.main-slider__arrow {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
}

.main-slider .slick-list,
.main-slider .slick-track,
.main-slider .slick-slide > div {
  height: 100%;
}

.main-slider .slick-dots {
  display: flex;
}

.main-slider .slick-dots li {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
}

.main-slider .slick-dots li.slick-active button {
  background-color: var(--main-color);
}

.main-slider .slick-dots li button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: var(--light-gray-color);
  color: transparent;
  transition: background-color 0.3s;
}

.main {
  display: flex;
  flex: 1 1 auto;
  width: calc(100% - 264px);
}

.main__content {
  flex: 1 1 auto;
  width: calc(100% - 300px);
  padding-right: calc(var(--gap) * 3);
}

.main__aside {
  flex: 0 0 auto;
  width: 300px;
}

.nav-mobile {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 64px;
  display: none;
  background-color: var(--bg-color);
  box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.05);
}

.nav-mobile__item {
  display: flex;
  flex: 1 1 auto;
}

.nav-mobile__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1 1 auto;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--text-color);
}

.nav-mobile__icon {
  flex: 0 0 auto;
  width: 24px;
  height: 24px;
}

.nav-mobile__item--active .nav-mobile__link {
  background-color: var(--white-color);
  color: var(--main-color);
}

.nav__link {
  display: flex;
  padding: var(--gap) calc(var(--gap) * 1.5);
  height: 40px;
  border-radius: var(--border-radius-small);
  transition: background-color 0.3s;
}

.nav__link:hover,
.nav__link--active {
  background-color: var(--white-color);
}

.nav__icon {
  flex: 0 0 auto;
  width: 24px;
  height: 24px;
  margin-right: var(--gap);
  fill: var(--text-color);
}

.nav__text {
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: var(--text-color);
  transition: color 0.3s;
}

.nav__link:hover .nav__text,
.nav__link--active .nav__text {
  color: var(--main-color);
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir=rtl] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.social {
  display: flex;
  padding-bottom: calc(var(--gap) * 2);
  border-bottom: 1px solid var(--light-gray-color);
}

.social__list {
  display: flex;
}

.social__item {
  margin-right: calc(var(--gap) * 1.5);
}

.social__item:last-child {
  margin-right: 0;
}

.social__link {
  display: flex;
  width: 24px;
  height: 24px;
  fill: var(--text-color);
}

.social__aside {
  margin-left: auto;
}

.subtitle-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 36px;
}

.subtitle-block__title {
  font-family: var(--font-raleway);
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--text-color);
}

.subtitle-block__button {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: var(--text-color);
  padding: var(--gap) 0;
}

.tags {
  display: flex;
  flex-wrap: wrap;
}

.tags__item {
  margin-right: var(--gap);
}

.tags__item:last-child {
  margin-right: 0;
}

.tags__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--text-light-color);
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 1600px) {
  .container {
    max-width: 1264px;
  }

  .main-slider {
    height: 188px;
  }

  .main-slider__img {
    width: 235px;
    height: 180px;
    margin-right: calc(var(--gap) * 3);
  }

  .main-slider__content {
    padding: calc(var(--gap) * 3);
    padding-right: calc(var(--gap) * 12);
  }

  .main-slider__title {
    font-size: 32px;
    line-height: 38px;
  }

  .main-slider__text {
    font-size: 16px;
    line-height: 20px;
  }

  .main-slider__arrow {
    width: 16px;
    height: 16px;
  }

  .main-slider .slick-dots li {
    width: 16px;
    height: 16px;
  }

  .main-slider .slick-dots li button {
    width: 8px;
    height: 8px;
  }
}

@media screen and (max-width: 768px) {
  .aside-menu-button {
    transform: translate(0%, -50%);
  }

  .open-aside-menu .aside-menu-button {
    transform: translate(100%, -50%);
  }

  .aside {
    display: none;
  }

  .cards {
    justify-content: center;
    margin: 0 -4px;
  }

  .cards__item {
    margin: 0 calc(var(--gap) * 0.5) calc(var(--gap) * 1.5);
  }

  .container--main {
    padding: 94px calc(var(--gap) * 0.5) calc(var(--gap) * 9);
  }

  .header {
    height: 86px;
    padding: calc(var(--gap) * 1.5) 0;
  }

  .header__container {
    align-items: flex-end;
    padding-left: calc(var(--gap) * 2);
  }

  .header__logo {
    margin-bottom: calc(var(--gap) * 0.5);
  }

  .header__nav {
    display: none;
  }

  .header__close-button {
    display: block;
  }

  .header__user-info {
    display: none;
  }

  .open-aside-menu .header__user-photo {
    opacity: 0;
  }

  .open-aside-menu .header__close-button:after,
  .open-aside-menu .header__close-button:before {
    width: 40px;
  }

  .main-slider {
    height: 141px;
  }

  .main-slider__list {
    padding: 0;
  }

  .main-slider__img {
    position: absolute;
    width: 208px;
    height: 160px;
    top: -10px;
    right: -30px;
    margin-right: 0;
    opacity: 0.2;
  }

  .main-slider__title {
    font-size: 24px;
    line-height: 24px;
  }

  .main-slider__text {
    font-size: 13px;
    line-height: 16px;
  }

  .main-slider__navigation {
    bottom: calc(var(--gap) * 0.5);
  }

  .main-slider__arrow {
    display: none;
  }

  .main__content {
    padding-right: 0;
  }

  .main__aside {
    position: fixed;
    top: 86px;
    transform: translateX(100%);
    transition: transform 0.6s, opacity 0.6s;
    height: calc(100vh - 86px);
    width: 100vw;
    right: 0;
    overflow-y: auto;
    background-color: var(--bg-color);
    padding: var(--gap) calc(var(--gap) * 0.5);
    opacity: 0;
    padding-bottom: 64px;
    z-index: 10;
  }

  .open-aside-menu .main__aside {
    opacity: 1;
    transform: translateX(0%);
  }

  .nav-mobile {
    display: flex;
  }

  .open-aside-menu {
    overflow: hidden;
  }
}

@media screen and (max-width: 414px) {
  .cards__item {
    width: 100%;
  }

  .subtitle-block__button {
    display: none;
  }
}